





















































































































































.push-modal {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 1000;
	display: none;

	&.show {
		.push-modal-content {
			transform: translateX(0);
		}
	}
}
.push-modal-mask {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
}
.push-modal-content {
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	display: flex;
	flex-direction: column;
	min-width: 300px;
	background-color: #fff;
	box-shadow: 5px 0 10px 0 rgba(0,0,0,0.1);
	transform: translateX(-100%);
	transition: transform .3s ease-in-out;
}
.push-modal-header {
	display: flex;
	align-items: center;
	padding: 20px;
	border-bottom: 1px solid #eaeaea;
}
.push-modal-title {
	flex: 1;
	margin: 0;
	font-size: 16px;
	font-weight: normal;
	line-height: 1;
}
.push-modal-dismiss-btn {
	font-size: 12px;
	transform: scale(2);
	cursor: pointer;
	color: #999;

	&:hover {
		color: #000;
	}
}
.push-modal-body {
	flex: 1;
	padding: 20px;
	overflow-y: auto;
}
.push-modal-footer {
	padding: 15px 20px;
	border-top: 1px solid #eaeaea;
	text-align: center;

	.el-button + .el-button {
		margin-left: 30px;
	}
}



