



































.detail-item {
	display: flex;
	align-items: stretch;
	margin: 0;
	border: solid #ddd;
	border-width: 0 1px 1px;

	&:first-child {
		border-top-width: 1px;
	}

	> dt {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		padding: 8px;
		border-right: 1px solid #ddd;
		background-color: #ecf5ff;
	}

	> dd {
		flex: 1;
		margin: 0;
		padding: 8px;
		white-space: pre-wrap;
	}
}



