	height: 100%;	

	.aside {
		transition: all .2s;
		background-color: #304152;
		box-shadow: 2px 0 6px rgba(0, 21, 41, 0.35);
		position: relative;
		z-index: 20;

		a {
			color: inherit;
		}

		.logo {
			height: 60px;
			padding-left: 20px;
			line-height: 60px;
			white-space: nowrap;
			overflow: hidden;
			color: #fff;

			i, h1 {
				display: inline-block;
				vertical-align: middle;
			}
			i {
				font-size: 30px;
			}		
			h1 {
				margin: 0 0 0 10px;
				font-size: 20px;
			}
		}

		.el-menu {
			padding: 10px 0;
			border: 0;
			transition: width .2s;
		}

		.el-menu-item {
			height: 40px;
			line-height: 40px;

			i {
				width: 1.2em;
				color: inherit;
			}
		}

		&.collapsed {
			.logo {
				padding-left: 16px;

				h1 {
					display: none;
				}
			}

			.el-menu-item {
				font-size: 16px;

				span {
					display: none;
				}
			}
		}
	}	

	.content {
		flex-direction: column;
		overflow-y: auto;
	}

	.header {
		flex: 0 0 50px;
		display: flex;
		padding: 0;
		line-height: 50px;
		background-color: #fff;
		box-shadow: 2px 0 6px rgba(0, 21, 41, 0.35);
		
		.aside-trigger {
			display: block;
			padding: 0 20px;
			margin-right: 20px;
			font-size: 18px;
			cursor: pointer;

			&:hover {
				color: #1890ff;
				background: #e6f7ff;
			}
		}

		> h2 {
			flex: 1;
			margin: 0;
			font-size: 16px;
			font-weight: 500;
		}

		.user-info {
			font-size: 14px;
		}

		.logout {
			display: inline-block;
			color: inherit;
			padding: 0 20px;
			cursor: pointer;

			&:hover {
				color: #1890ff;
				background: #e6f7ff;
			}
		}
	}

	.main {
		position: relative;
		padding: 20px;
		flex: 1;
	}

	@media only screen and (max-width: 992px) {
		.main {
			padding: 20px;
		}
	}
}



