	margin-bottom: 20px;
	box-shadow: 0 0 5px 1px rgba(0,0,0,0.1);
	background-color: #fff;
}
.block-header {
	display: flex;
	align-items: center;
	padding: 0 20px;
	border-bottom: 1px solid #eaeaea;
}
.block-title {
	flex: 1;
	margin: 0;
	padding: 15px 0;
	line-height: 32px;
	font-size: 15px;
	font-weight: normal;
}
.block-tabs {
	flex: 1;
	display: flex;
	position: relative;
}
.block-tab-item {
	display: block;
	padding: 15px 20px;
	line-height: 32px;
	cursor: pointer;

	&:hover {
		color: #40a9ff;
	}

	&.active::after {
		color: #1890ff;
		display: block;
	}
}
.block-tab-active-bar {
	position: absolute;
	left: 0;
	bottom: 0;
	border-top: 2px solid #40a9ff;
	transition: all .2s;
}
.block-action {
	padding: 15px 0;
}
.block-body {
	padding: 20px;
}



